import { BehaviorSubject } from "rxjs";
import { Model, SelectableModel } from "./Model";
import { Box3, Scene, Object3D } from "three";
import { Selection } from "./Selection";

export class Models {
    public worldBoundingBox: BehaviorSubject<Box3>;
    private _models: Map<Model, Box3>;
    private _modelContainer: Object3D;

    constructor(scene: Scene, private selection: Selection) {
        this.worldBoundingBox = new BehaviorSubject(new Box3());
        this._models = new Map();
        this._modelContainer = new Object3D();
        scene.add(this._modelContainer);
        this.setupSelection(selection);
    }

    private setupSelection(selection: Selection): void {
        selection.subscribe(() => {
            for (const [m] of this._models) {
                const model = m as Model & SelectableModel;
                if (!model.setSelection) continue;

                const ids = selection.get(model.name);
                if (!ids || ids.length === 0)
                    model.clearSelection();
                else
                    model.setSelection(ids);
            }
        });
    }

    async add(model: Model, addToScene: boolean = true): Promise<void> {
        if (this.get(model.name))
            throw new Error("A model with the same id is already loaded. " + model.name);

        this._models.set(model, await model.getBoundingBox());
        model.subscribeToBoundingBox((box) => {
            this._models.set(model, box);
            this.recalculateWorldBoundingBox()
        });
        if (addToScene)
            this._modelContainer.add(model);
    }

    async remove(modelId: string): Promise<void> {
        const model = this.get(modelId);
        if (!model) throw new Error(`Model ${modelId} not found`);

        this._models.delete(model);
        this.recalculateWorldBoundingBox();
        this._modelContainer.remove(model);
        this.selection.delete(modelId);
        await model.dispose();
    }

    get(modelId: string): Model {
        return Array.from(this._models.keys()).find(m => m.name === modelId);
    }

    getIterable(): IterableIterator<Model> {
        return this._models.keys();
    }

    getBoundingBoxesIterable(): IterableIterator<Box3> {
        return this._models.values();
    }

    private recalculateWorldBoundingBox(): void {
        this.worldBoundingBox.value.makeEmpty();
        this._models.forEach(box => this.worldBoundingBox.value.union(box));
        this.worldBoundingBox.next(this.worldBoundingBox.value);
    }
}
