import {WebGLRenderTarget} from "three";
import {Texture} from "three/src/textures/Texture";

export class MultiRenderTarget extends WebGLRenderTarget {
    readonly isMultiRenderTarget = true;

    constructor(public textures: Texture[], depthBuffer: boolean, stencilBuffer: boolean) {
        super(textures ? textures[0].image.width : undefined, textures ? textures[0].image.height : undefined);
        if (textures) this.texture = textures[0];
        this.depthBuffer = depthBuffer;
        this.stencilBuffer = stencilBuffer;
    }

    copy(source: MultiRenderTarget): any {
        super.copy(source);
        this.textures = [this.texture];
        for (let i = 1, il = source.textures.length; i < il; i++)
            this.textures[i] = source.textures[i].clone();
        return this;
    }

    setSize(width: number, height: number): void {
        if (this.width !== width || this.height !== height) {
            this.width = width;
            this.height = height;

            for (let i = 0, il = this.textures.length; i < il; i++) {
                this.textures[i].image.width = width;
                this.textures[i].image.height = height;
            }

            this.dispose();
        }
        this.viewport.set( 0, 0, width, height );
        this.scissor.set( 0, 0, width, height );
    }
}
