import {SettingsProxyHandler} from "./SettingsProxyHandler";
import {Settings} from "./common";

interface PageViewData {
    hitType: "pageview";
    title?: string;
    location?: string;
    page?: string;
}

interface EventData {
    hitType: "event";
    eventCategory?: string;
    eventAction?: string;
    eventLabel?: string;
    eventValue?: number;
}

interface ExceptionData {
    hitType: "exception";
    exDescription?: string;
    exFatal?: boolean;
}

export class Analytics {
    private keywords: string[] = [];

    constructor(private settingsProxy: SettingsProxyHandler<Settings>) {
        this.init();
        this.command("create", "UA-155752318-1", "auto");
        this.send({hitType: "pageview"});

        window.addEventListener("error", (event: ErrorEvent) => {
            if (!event.error) return;

            const keyword = this.containsViewerKeyword(event.error.stack);
            if (keyword)
                this.logError(event.error.stack + ` (keyword: ${keyword})`);
        });
    }

    private containsViewerKeyword(errorStack: string): string {
        for (const k of this.keywords)
            if (errorStack.includes(k)) return k;
    }

    addViewerErrorStackKeyword(keyword: string): void {
        this.keywords.push(keyword);
    }

    private init(): void {
        if (!this.settingsProxy.settings.analyticsEnabled) return;

        ((i: Window, s: Document, o: string, g: string, r: string, a: any, m: any) => {
            // @ts-ignore
            i['GoogleAnalyticsObject']=r;i[r]=i[r]||function(){(i[r].q=i[r].q||[]).push(arguments)}, i[r].l=new Date().getTime();a=s.createElement(o), m=s.getElementsByTagName(o)[0];a.async=1;a.src=g;m.parentNode.insertBefore(a,m)
        })(window,document,'script','https://www.google-analytics.com/analytics.js','gawv', undefined, undefined);
    }

    private command(cmd: string, param1: any, param2?: any): void {
        if (!this.settingsProxy.settings.analyticsEnabled) return;
        // @ts-ignore
        gawv(cmd, param1, param2);
    }

    private send(data: EventData | ExceptionData | PageViewData): void {
        this.command("send", data);
    }

    logLoadEvent(modelId: string): void {
        this.send({hitType: "event", eventCategory: "Model", eventAction: "Load", eventLabel: modelId});
    }

    logError(message: string): void {
        this.send({
            hitType: "exception",
            exDescription: message,
        });
        this.send({
            hitType: "event",
            eventCategory: "Error",
            eventAction: "Error",
            eventLabel: message
        });
    }
}
