import {WVBackground} from "./WVBackground";
import {EdgesAndSsaoRenderPass} from "./EdgesAndSsaoRenderPass";
import {DepthPeelingRenderPass} from "./DepthPeelingRenderPass";
import {SelectionEffectPass} from "./SelectionEffectPass";
import {FXAAPass} from "./FXAAPass";
import {Api} from "../Api";
import {LinearFilter, RGBAFormat, WebGLRenderTarget} from "three";
import {Pass} from "three/examples/jsm/postprocessing/Pass";
import {RenderingManager} from "./RenderingManager";

export class PassComposer {
    depthPeelingRenderPass: DepthPeelingRenderPass;
    private selectionEffectPass: SelectionEffectPass;
    private fxaaPass: FXAAPass;
    private antialias: boolean;

    private beforeSelectionPasses: Pass[] = [];
    private afterSelectionPasses: Pass[] = [];
    private beforeAntialiasingPasses: Pass[] = [];
    private afterAntialiasingPasses: Pass[] = [];

    renderTarget: WebGLRenderTarget;
    mainPassRenderTarget: WebGLRenderTarget;

    constructor(private api: Api, private renderingManager: RenderingManager) {
        this.updateAntialias();
        api.settingsProxy.addListener("antialias", () => this.updateAntialias());
        this.renderTarget = new WebGLRenderTarget(renderingManager.width, renderingManager.height, {
            minFilter: LinearFilter,
            magFilter: LinearFilter,
            format: RGBAFormat,
            stencilBuffer: false
        });

        const background = new WVBackground(renderingManager, api.settingsProxy, api.camera);
        const edgesAndSsaoPass = new EdgesAndSsaoRenderPass(api.settingsProxy, renderingManager, api.scene, api.camera);
        this.depthPeelingRenderPass = new DepthPeelingRenderPass(renderingManager, api.settingsProxy, api.scene, api.camera, background, edgesAndSsaoPass);
        this.selectionEffectPass = new SelectionEffectPass(api.camera, api.settingsProxy.settings.selectionColor);
        this.api.selectionEffectPass = this.selectionEffectPass;
        this.fxaaPass = new FXAAPass(); // Note: MSAA is force enabled in VR mode, enabling both FXAA and MSAA at the same time is a bad idea
    }

    private updateAntialias(): void {
        this.antialias = this.api.settingsProxy.settings.antialias === undefined
            ? devicePixelRatio <= 1 && !this.renderingManager.isMobile()
            : this.api.settingsProxy.settings.antialias;
    }

    render(): void {
        const renderer = this.renderingManager.renderer;
        const rt = this.antialias ? this.renderTarget : null;

        this.depthPeelingRenderPass.render(renderer, this.mainPassRenderTarget || rt);
        this.renderPasses(this.beforeSelectionPasses, rt, !this.antialias);
        this.selectionEffectPass.render(renderer, rt);
        this.renderPasses(this.afterSelectionPasses, rt, !this.antialias);
        this.renderPasses(this.beforeAntialiasingPasses, rt, !this.antialias);
        if (this.antialias) this.fxaaPass.render(renderer, null, rt, undefined, undefined);
        this.renderPasses(this.afterAntialiasingPasses, this.renderTarget, true);
    }

    private renderPasses(passes: Pass[], renderTarget: WebGLRenderTarget, renderToScreen: boolean): void {
        for (const p of passes) {
            p.renderToScreen = renderToScreen;
            p.render(this.renderingManager.renderer, p.needsSwap ? renderTarget : null, p.needsSwap ? null : renderTarget, undefined, undefined);
        }
    }

    setSize(width: number, height: number): void {
        this.updateAntialias();
        this.renderTarget.setSize(width, height);
        if (this.mainPassRenderTarget) this.mainPassRenderTarget.setSize(width, height);
        this.depthPeelingRenderPass.setSize(width, height);
        this.selectionEffectPass.setSize(width, height);
        this.fxaaPass.setSize(width, height);
        this.beforeSelectionPasses.forEach(p => p.setSize(width, height));
    }

    addPassBeforeSelection(pass: Pass): void {
        this.beforeSelectionPasses.push(pass);
    }

    addPassAfterSelection(pass: Pass): void {
        this.afterSelectionPasses.push(pass);
    }

    addPassBeforeAntialiasing(pass: Pass): void {
        this.beforeAntialiasingPasses.push(pass);
    }

    addPassAfterAntialiasing(pass: Pass): void {
        this.afterAntialiasingPasses.push(pass);
    }
}
