import {
    Vector3,
    WebGLRenderer,
    Intersection,
    Matrix4,
    Ray,
    LineSegments,
    Frustum,
    Line3,
    InterleavedBufferAttribute,
    Box3,
    InterleavedBuffer,
} from "three";
import { LineMaterial } from "three/examples/jsm/lines/LineMaterial";
import { LineGeometry } from "three/examples/jsm/lines/LineGeometry";
import {Caster} from "../Picker/Caster";
import {LineSegments2} from "three/examples/jsm/lines/LineSegments2";

export class MeshLine extends LineSegments2 {
    geometry: LineGeometry;
    material: LineMaterial;

    private _inverseMatrix = new Matrix4();
    private _ray = new Ray();
    private _frustum = new Frustum();
    private _box = new Box3();
    private vStart = new Vector3();
    private vEnd = new Vector3();
    private interPoint = new Vector3();
    private segmentPoint = new Vector3();

    constructor(geometry: LineGeometry, material: LineMaterial) {
        super(geometry, material);
        this.material = material;
        material.dashed ? material.defines.USE_DASH = "": delete material.defines.USE_DASH;
        this.onBeforeRender = (renderer: WebGLRenderer) => renderer.getSize(material.resolution);
    }

    update(vectors: Vector3[] | Float32Array): void {
        let positions: Float32Array;

        if (vectors instanceof Float32Array) {
            positions = vectors
        }
        else {
            const positionsAttribute = this.geometry.attributes.instanceEnd as InterleavedBufferAttribute;
            positions = positionsAttribute ? positionsAttribute.data.array as Float32Array : undefined;
            if (!positions || positions.length !== vectors.length * 3)
                positions = new Float32Array(vectors.length * 3);
            for (let i = 0; i < vectors.length; i++) vectors[i].toArray(positions, i * 3);
        }

        const instanceStartAttr = this.geometry.getAttribute("instanceStart");
        if (instanceStartAttr && instanceStartAttr.array.length !== positions.length * 2 - 6) {
            // TODO: LineGeometry does not support resizing, this is a hackaround. Avoid this if possible
            this.geometry = new LineGeometry();
        }
        this.geometry.setPositions(positions);
        if (this.material.dashed) this.computeLineDistances();
    }

    onUpload( callback: () => void ): void {
        const startBuffer = (this.geometry.attributes.instanceStart as InterleavedBufferAttribute).data as any as InterleavedBuffer;
        (startBuffer as any).onUploadCallback = callback;
        const endBuffer = (this.geometry.attributes.instanceEnd as InterleavedBufferAttribute).data as any as InterleavedBuffer;
        (endBuffer as any).onUploadCallback = callback;
    }

    raycast(caster: Caster, intersects: Intersection[]): void {
        const geometry = this.geometry;
        const matrixWorld = this.matrixWorld;

        this._box.copy(geometry.boundingBox);
        this._box.applyMatrix4(matrixWorld);

        if (!caster.frustum.intersectsBox(this._box)) return;

        this._inverseMatrix.copy(matrixWorld).invert();
        this._ray.copy(caster.ray).applyMatrix4(this._inverseMatrix);
        this._frustum.copy(caster.frustum);
        for (const plane of this._frustum.planes) plane.applyMatrix4(this._inverseMatrix);

        const step = (this && this instanceof LineSegments) ? 2 : 1;

        const positions = (geometry.attributes.instanceEnd as InterleavedBufferAttribute).data.array;

        for (let i = 0, l = positions.length / 3 - 1; i < l; i += step) {
            this.vStart.fromArray(positions, 3 * i);
            this.vEnd.fromArray(positions, 3 * i + 3);

            if (!this.intersect(this._ray, this._frustum, this.vStart, this.vEnd, this.interPoint)) continue;

            this.interPoint.applyMatrix4(this.matrixWorld);
            const distance = caster.ray.origin.distanceTo(this.interPoint);
            if (distance < caster.near || distance > caster.far) continue;

            intersects.push({
                distance: distance,
                point: this.interPoint.clone(),
                index: i,
                object: this
            });
        }
    }

    private intersect(ray: Ray, frustum: Frustum, lineStart: Vector3, lineEnd: Vector3, target: Vector3): boolean {
        ray.distanceSqToSegment(lineStart, lineEnd, target, this.segmentPoint);
        return frustum.containsPoint(this.segmentPoint);
    }
}
