import { Pan } from "./Pan";
import { Zoom } from "./Zoom";
import { Orbit } from "./Orbit";
import { Fly } from "./Fly";
import { VRNavigation } from "./VRNavigation";
import { SelectionTool } from "./SelectionTool";
import { Tool, WebglViewerEventName } from "../common";
import { Api } from "../Api";
import { AreaSelectionTool } from "./AreaSelectionTool";
import { WVCamera } from "../Rendering/WVCamera";
import { debounceTime, tap } from "rxjs/operators";
import { ApiContainer } from "../ApiContainer";
import { BehaviorSubject } from "rxjs";
import { PickingTool } from "./PickingTool";
import { GamepadNavigation } from "./GamepadNavigation";

export class ToolManager {
    tools = new ApiContainer<Tool>();
    private _camera: WVCamera;
    private _activeTool: Tool;
    private _conflictingTools: Tool[] = [];

    constructor(private _api: Api) {
        this._camera = this._api.camera;

        // Navigation tools
        this.addTool(new Orbit(this._api.inputHandler, _api.camera, _api.models.worldBoundingBox));
        this.addTool(new Fly(this._api.inputHandler, _api.camera));
        this.addTool(new Pan(this._api));
        this.addTool(new Zoom(this._api));
        this.addTool(new VRNavigation(this._api));
        this.addTool(new GamepadNavigation(this._api));

        // Interaction tools
        this.addTool(new SelectionTool(this._api));
        this.addTool(new AreaSelectionTool(this._api));
        this.addTool(new PickingTool(this._api.inputHandler, _api.cursor, _api.eventDispatcher));
        this.activateDefaultTool();

        this._startEmittingNavigationEvents();
        this._subscribeToDragging();
    }

    private _startEmittingNavigationEvents(): void {
        let navStarted = false;
        const cameraSubject = new BehaviorSubject<WVCamera>(this._api.camera);
        this._api.camera.subscribe(() => cameraSubject.next(this._api.camera));

        cameraSubject.pipe(
            tap(() => {
                if (!navStarted) this._api.eventDispatcher.navigation(true);
                navStarted = true;
            }),
            debounceTime(100),
            tap(() => {
                this._api.eventDispatcher.navigation(false);
                navStarted = false;
            })
        ).subscribe();
    }

    private _subscribeToDragging(): void {
        this._api.eventDispatcher.subscribe(WebglViewerEventName.DragStart, e => this._disableConflictingTools(e));
        this._api.eventDispatcher.subscribe(WebglViewerEventName.DragEnd, () => this._enableConflictingTools());
    }

    private _disableConflictingTools(e: CustomEvent<Tool>): void {
        for (const toolName in this.tools) {
            if (!this.tools.hasOwnProperty(toolName)) continue;
            const tool = this.tools[toolName] as Tool;
            if (tool === e.detail) continue;

            // disable conflicting tools
            if (tool.enabled) {
                tool.enabled = false;
                this._conflictingTools.push(tool);
            }
        }
    }

    private _enableConflictingTools(): void {
        for (const tool of this._conflictingTools)
            tool.enabled = true;
        this._conflictingTools.length = 0;
    }

    addTool(tool: Tool): void {
        if (this.tools.hasOwnProperty(tool.name)) throw new Error(`Tool ${tool.name} already exists`);
        this.tools[tool.name] = tool;
        tool.onFinished = () => this.activateDefaultTool();
    }

    activateDefaultTool(): void {
        this.activeTool = "selection";
    }

    set activeTool(toolName: string) {
        if (this._activeTool) this._activeTool.enabled = false;
        this._activeTool = this.tools[toolName] as Tool;
        if (this._activeTool) this._activeTool.enabled = true;
        this._api.eventDispatcher.dispatch(new CustomEvent(WebglViewerEventName.ActiveToolChanged));
    }

    get activeTool(): string {
        return this._activeTool.name;
    }
}
