import { StickyElement } from "./CustomObjects/StickyElement";

import { Api } from "./Api";

export class HtmlElementPositioner {
    private readonly _htmlObjects: Set<StickyElement>;

    constructor(private _api: Api) {
        this._htmlObjects = new Set();
        _api.renderingManager.addBeforeRenderListener(() => this.update());
    }

    addElement(object: StickyElement): void {
        if (!this._htmlObjects.has(object)) {
            this._api.container.appendChild(object);
            this._htmlObjects.add(object);
        }
    }

    removeElement(object: StickyElement): void {
        if (this._htmlObjects.has(object)) {
            this._api.container.removeChild(object);
            this._htmlObjects.delete(object);
        }
    }

    private update(): void {
        const parentClientRect = this._api.container.getBoundingClientRect();
        for (const htmlObject of this._htmlObjects)
            htmlObject.updatePosition(parentClientRect);
    }
}
