import { Vector3 } from "three";
import { MouseButton } from "../common";
import { InputHandler, PointerInput } from "../InputHandler";
import { WVCamera } from "../Rendering/WVCamera";
import { AbstractOrbit } from "./Orbit";
import { tap, pairwise } from "rxjs/operators";
import { Observable, Subscription, merge, animationFrameScheduler } from "rxjs";
import { msElapsed } from "../Animation";
import {Vector3Const} from "../Helpers/common-utils";

export class Fly extends AbstractOrbit {
    static get Name(): string { return "fly" };

    private _keyHandle: Subscription;
    private _keyObservable: Observable<KeyboardEvent>;
    private _moveHandle: Subscription;
    private _moveObservable: Observable<any>;
    private _dir = new Vector3();
    private _fly = new Vector3();

    private _speedIncrease = 0.00001;
    private _minSpeed = 0.002;
    private _maxSpeed = 0.02;
    private _speed = this._minSpeed;

    get name(): string {
        return Fly.Name;
    }

    constructor(inputs: InputHandler, camera: WVCamera) {
        super(inputs, camera);
        this.mouseButton = MouseButton.right;
        this.touchCount = 3;
        this._keyObservable = merge(inputs.keyDown$, inputs.keyUp$).pipe(tap(e => this.processKey(e)));
        this._moveObservable = msElapsed(animationFrameScheduler).pipe(
            pairwise(),
            tap(t => this.move(t[1] - t[0]))
        );
        this.enabled = true;
    }

    private move(delta: number): void {
        if (this._dir.lengthSq() === 0) return;

        this._speed = Math.min(this._speed + delta * this._speedIncrease, this._maxSpeed);
        this._fly.copy(this._dir).multiplyScalar(delta * this._speed);
        this._fly.applyQuaternion(this._camera.quaternion);
        this._camera.position.add(this._fly);
        this._camera.callListeners();
    }

    private processKey(event: KeyboardEvent): void {
        const sign = event.type === "keydown" ? 1 : -1;
        switch (event.code) {
            case "KeyW": this._dir.z += -sign; break;
            case "KeyS": this._dir.z += sign; break;
            case "KeyA": this._dir.x += -sign; break;
            case "KeyD": this._dir.x += sign; break;
            case "KeyQ": this._dir.y += -sign; break;
            case "KeyE": this._dir.y += sign; break;
        }
        this._maxSpeed = event.shiftKey ? 0.1 : 0.02;
        this._speedIncrease = event.shiftKey ? 0.0001 : 0.000005;
        if (this._dir.equals(Vector3Const.zero)) this._speed = this._minSpeed;
    }

    protected async moveCallback(event: PointerInput): Promise<PointerInput> {
        // on macOS unfocused window might receive mouse events, but not keyboard events. This confuses user, so lets disable all events
        if (!document.hasFocus()) return;

        return super.moveCallback(event);
    }

    protected async pick(event: PointerInput): Promise<PointerInput> {
        return event;
    }

    protected calculateRotationPoint(event: PointerInput): void {
        this._rotationPoint = this._camera.position;
        super.calculateRotationPoint(event);
        this._keyHandle = this._keyObservable.subscribe();
        this._moveHandle = this._moveObservable.subscribe();
    }

    public upCallback(event: PointerInput): PointerInput {
        this._dir.set(0, 0, 0);
        if (this._keyHandle) this._keyHandle.unsubscribe();
        if (this._moveHandle) this._moveHandle.unsubscribe();
        return event;
    }
}
