import {
    DataTexture,
    DepthTexture,
    HalfFloatType, LinearFilter,
    Material,
    RGBAFormat, RGBFormat,
    Scene,
    ShaderMaterial, UnsignedByteType,
    Vector2,
    WebGLRenderer,
    WebGLRenderTarget
} from "three";
import {MultiRenderTarget} from "./MultiRenderTarget";
import {EffectPass} from "./EffectPass";
import {RenderingManager} from "./RenderingManager";
import {createDepthTexture} from "./DepthPeelingRenderPass";
import {WVMeshMaterial} from "./WVMeshMaterial";
import {WVCamera} from "./WVCamera";
import {Texture} from "three/src/textures/Texture";
import {TextureDataType} from "three/src/constants";
import {SettingsProxyHandler} from "../SettingsProxyHandler";
import {Settings} from "../common";
import {disposeImage} from "../Helpers/common-utils";

// language=GLSL
const vertexShader = `
    varying vec2 vUv;
    varying vec2 vOffsetUvs[4];
    uniform vec2 texelSize;

    void main() {
        vUv = uv;
        vOffsetUvs[0] = vec2(texelSize.x, 0);
        vOffsetUvs[1] = vec2(-texelSize.x, 0);
        vOffsetUvs[2] = vec2(0, texelSize.y);
        vOffsetUvs[3] = vec2(0, -texelSize.y);
        gl_Position = vec4(position.xy, 0.0, 1.0);
    }
`;

// language=GLSL
const fragmentShaderCommon = `
    varying vec2 vUv;
    varying vec2 vOffsetUvs[4];
    uniform highp sampler2D normalEdgeFadeBuffer;
    uniform highp sampler2D depthBuffer;
    uniform vec2 cameraNearFar;
    uniform mat4 uProjectionInverse;

    // Camera relative position
    vec3 computePosition(vec2 coord, float depthSample) {
        // normalized device coordinates
        vec4 ndc = vec4((vec3(coord, depthSample) - 0.5) * 2.0, 1.0);
        vec4 clip = uProjectionInverse * ndc;
        return clip.xyz / clip.w;
    }

    vec3 getNormal(vec4 normalSample) {
        // treat background as surface with normal towards camera
        return normalSample.xyz == vec3(0) ? vec3(0.0, 0.0, 1.0) : normalSample.xyz;
    }
`;

export class EdgesAndSsaoRenderPass {
    private multiRenderTarget: MultiRenderTarget;
    private ssaoRenderTarget: WebGLRenderTarget;
    private ssaoSimpleRenderTarget: WebGLRenderTarget;
    private edgesPass: EffectPass;
    private ssaoPass: EffectPass;
    private ssaoSimplePass: EffectPass;
    private noiseTexture: Texture;
    private texelSize = new Vector2();
    private cameraNearFar = new Vector2();

    get colorTexture(): Texture {
        return this.multiRenderTarget.textures[0];
    }

    get normalTexture(): Texture {
        return this.multiRenderTarget.textures[1];
    }

    get depthTexture(): DepthTexture {
        return this.multiRenderTarget.depthTexture;
    }

    constructor(private settingsProxy: SettingsProxyHandler<Settings>, private renderingManager: RenderingManager, private scene: Scene, private camera: WVCamera) {
        this.init();
        this.settingsProxy.addListener("renderEdges", () => this.init());
    }

    private init(): void {
        if (this.renderEdges && !this.multiRenderTarget) {
            this.multiRenderTarget = new MultiRenderTarget(
                [this.createTexture(UnsignedByteType), this.createTexture(HalfFloatType)],
                true, false);
            this.multiRenderTarget.depthTexture = createDepthTexture(this.renderingManager);
            this.noiseTexture = this.createNoiseTexture();
            this.ssaoRenderTarget = new WebGLRenderTarget(1, 1, {
                minFilter: LinearFilter,
                magFilter: LinearFilter
            });
            this.ssaoSimpleRenderTarget = this.ssaoRenderTarget.clone();
            this.ssaoPass = new EffectPass(this.createSsaoMaterial(false));
            this.ssaoSimplePass = new EffectPass(this.createSsaoMaterial(true));
            this.edgesPass = new EffectPass(this.createEdgesMaterial());
        }
    }

    private get renderEdges(): boolean {
        return this.settingsProxy.settings.renderEdges && this.renderingManager.isWebgl2();
    }

    private createNoiseTexture(): Texture {
        const size = 100;
        const noiseData = new Uint8Array(size * size * 3);
        for (let i = 0; i < noiseData.length; ++i)
            noiseData[i] = Math.random() * 255;
        const t = new DataTexture(noiseData, size, size, RGBFormat);
        t.onUpdate = disposeImage;
        return t;
    }

    private createTexture(type: TextureDataType): Texture {
        return new Texture(
            {width: this.renderingManager.width, height: this.renderingManager.height} as any,
            undefined,
            undefined,
            undefined,
            LinearFilter,
            LinearFilter,
            RGBAFormat,
            type,
            undefined,
            undefined
        );
    }

    private createEdgesMaterial(): ShaderMaterial {
        const mat = new ShaderMaterial({
            uniforms: {
                inputBuffer: { value: this.multiRenderTarget.textures[0] },
                normalEdgeFadeBuffer: { value: this.multiRenderTarget.textures[1] },
                depthBuffer: { value: this.multiRenderTarget.depthTexture },
                ssaoBuffer: { value: this.ssaoRenderTarget.texture },
                cameraNearFar: { value: this.cameraNearFar },
                texelSize: { value: this.texelSize },
                uProjectionInverse: { value: this.camera.projectionMatrixInverse }
            },
            vertexShader: vertexShader,
            // language=GLSL
            fragmentShader: `
                ${fragmentShaderCommon}

                uniform sampler2D inputBuffer;
                uniform sampler2D ssaoBuffer;
                
                float distancePointToPlane(vec3 planeNormal, float planeConstant, vec3 pointPos) {
                    return abs(dot(planeNormal, pointPos) + planeConstant);
                }

                void main() {
                    vec4 color = texture2D(inputBuffer, vUv);
                    vec4 normalEdgeFadeSample = texture2D(normalEdgeFadeBuffer, vUv);
                    vec3 normal = getNormal(normalEdgeFadeSample);
                    float edgeFade = normalEdgeFadeSample.w;
                    float depthSample = texture2D(depthBuffer, vUv).x;
                    vec3 position = computePosition(vUv, depthSample);
                    float planeConstant = -dot(position, normal);
                    bool isEdge = false;

                    for (int i = 0; i < vOffsetUvs.length(); i++) {
                        vec2 uv = vUv + vOffsetUvs[i];
                        vec4 offsetNormalEdgeFadeSample = texture2D(normalEdgeFadeBuffer, uv);
                        vec3 offsetNormal = getNormal(offsetNormalEdgeFadeSample);
                        vec3 offsetPosition = computePosition(uv, texture2D(depthBuffer, uv).x);
                        edgeFade = max(edgeFade, offsetNormalEdgeFadeSample.w);

                        float normalDot = abs(dot(normal, offsetNormal));
                        float depthDelta = distancePointToPlane(normal, planeConstant, offsetPosition);
                        isEdge = normalDot < 0.9 ||
                            depthDelta > 0.01 &&
                            abs(depthDelta / position.z) > 0.01;// depth precision fix, removes noise
                        if (isEdge) break;
                    }

                    float occlusion = texture2D(ssaoBuffer, vUv).a;
                    color.rgb = color.rgb * (1.0 - occlusion);
                    
                    gl_FragColor = mix(color, vec4(color.rgb * 0.7, max(color.a, 0.4)), isEdge ? edgeFade : 0.0);
                    gl_FragDepthEXT = depthSample;
                }
            `,
            transparent: true,
            depthTest: true,
            depthWrite: true,
            premultipliedAlpha: true
        });
        mat.extensions.fragDepth = true;
        return mat;
    }

    private createSsaoMaterial(simple: boolean): ShaderMaterial {
        return new ShaderMaterial({
            uniforms: {
                normalEdgeFadeBuffer: { value: this.multiRenderTarget.textures[1] },
                depthBuffer: { value: this.multiRenderTarget.depthTexture },
                noiseBuffer: { value: this.noiseTexture },
                cameraNearFar: { value: this.cameraNearFar },
                texelSize: { value: this.texelSize },
                uProjectionInverse: { value: this.camera.projectionMatrixInverse }
            },
            defines: {
                SIMPLE: simple
            },
            vertexShader: vertexShader,
            // language=GLSL
            fragmentShader: `
                #define SIN45 0.707107

                ${fragmentShaderCommon}
                
                uniform sampler2D noiseBuffer;
                uniform vec2 texelSize;

                float getOcclusion(vec3 position, vec3 normal, vec2 uv) {
                    float uBias = 0.04;
                    vec2 uAttenuation = vec2(1.0, 1.0);

                    vec3 offsetPosition = computePosition(uv, texture2D(depthBuffer, uv).x);
                    vec3 positionVec = offsetPosition - position;
                    float intensity = max(dot(normal, normalize(positionVec)) - uBias, 0.0);
                    float attenuation = 1.0 / (uAttenuation.x + uAttenuation.y * length(positionVec));
                    return intensity * attenuation;
                }

                void main() {
                    vec3 normal = getNormal(texture2D(normalEdgeFadeBuffer, vUv));
                    float depthSample = texture2D(depthBuffer, vUv).x;
                    if (depthSample == 1.0) // background                     
                        discard;
                    
                    vec3 position = computePosition(vUv, depthSample);
                    vec2 rand = normalize(texelFetch(noiseBuffer, ivec2(mod(gl_FragCoord.xy, vec2(100))), 0).xy);
                    
                    float occlusionRadius = 32.0;
                    float occlusion = 0.0;

                    int offsCount = vOffsetUvs.length();
                    #ifdef SIMPLE
                        offsCount = 2;
                    #endif
                    for (int i = 0; i < offsCount; i++) {
                        vec2 k1 = reflect(vOffsetUvs[i], rand);
                        vec2 k2 = vec2(k1.x * SIN45 - k1.y * SIN45, k1.x * SIN45 + k1.y * SIN45);
                        k1 *= occlusionRadius;
                        k2 *= occlusionRadius;
                        
                        occlusion += getOcclusion(position, normal, vUv + k1);
                        occlusion += getOcclusion(position, normal, vUv + k2 * 0.75);
                        occlusion += getOcclusion(position, normal, vUv + k1 * 0.5);
                        occlusion += getOcclusion(position, normal, vUv + k2 * 0.25);
                    }

                    #ifdef SIMPLE
                        occlusion *= 0.8;
                    #endif
                    occlusion = clamp(occlusion / float(4 * offsCount), 0.0, 1.0);
                    gl_FragColor = vec4(occlusion);
                }
            `,
            depthTest: false,
            depthWrite: false,
        });
    }

    render(renderer: WebGLRenderer, renderTarget: WebGLRenderTarget, renderSsao: boolean, shouldRenderMaterial: (m: Material) => boolean): void {
        if (this.renderEdges)
            this.renderWithEdges(renderer, renderTarget, renderSsao, shouldRenderMaterial);
        else
            this.renderWithoutEdges(renderer, renderTarget, shouldRenderMaterial);
    }

    private renderWithoutEdges(renderer: WebGLRenderer, renderTarget: WebGLRenderTarget, shouldRenderMaterial: (m: Material) => boolean): void {
        let shouldRenderAnything = false;
        this.renderingManager.traverseMaterials((o, m) => {
            const srm = shouldRenderMaterial(m);
            shouldRenderAnything = shouldRenderAnything || srm;
            return m.visible = srm;
        });
        if (!shouldRenderAnything) return; // early optimization

        renderer.setRenderTarget(renderTarget);
        renderer.render(this.scene, this.camera);
    }

    private renderWithEdges(renderer: WebGLRenderer, renderTarget: WebGLRenderTarget, renderSsao: boolean, shouldRenderMaterial: (m: Material) => boolean): void {
        let shouldRenderAnything = false;
        // TODO: pass shouldRenderMaterial to webgl-viewer/src/Rendering/WebGLRenderer instead of traversing, should be faster
        this.renderingManager.traverseMaterials((o, m) => {
            const srm = shouldRenderMaterial(m);
            shouldRenderAnything = shouldRenderAnything || srm;
            return m.visible = !!(m as WVMeshMaterial).isWVMeshMaterial && srm;
        });
        if (!shouldRenderAnything) return; // early optimization

        renderer.setRenderTarget(this.multiRenderTarget);
        renderer.clear();
        renderer.render(this.scene, this.camera);

        this.cameraNearFar.set(this.camera.near, this.camera.far);

        this.renderSsao(renderer, renderSsao);
        this.edgesPass.render(renderer, renderTarget);

        this.renderingManager.traverseMaterials((o, m) => m.visible = !(m as WVMeshMaterial).isWVMeshMaterial && shouldRenderMaterial(m));
        renderer.setRenderTarget(renderTarget);
        renderer.render(this.scene, this.camera);
    }

    private renderSsao(renderer: WebGLRenderer, renderSsao: boolean): void {
        if (!renderSsao || !this.settingsProxy.settings.ssao) {
            // just clear the buffer
            this.edgesPass.getFullscreenMaterial().uniforms.ssaoBuffer.value = undefined;
        }
        else if (this.renderingManager.fullRender || !this.settingsProxy.settings.progressiveRendering) {
            this.ssaoPass.render(renderer, this.ssaoRenderTarget);
            this.edgesPass.getFullscreenMaterial().uniforms.ssaoBuffer.value = this.ssaoRenderTarget.texture;
        }
        else {
            this.ssaoSimplePass.render(renderer, this.ssaoSimpleRenderTarget);
            this.edgesPass.getFullscreenMaterial().uniforms.ssaoBuffer.value = this.ssaoSimpleRenderTarget.texture;
        }
    }

    setSize(width: number, height: number): void {
        if (this.multiRenderTarget) {
            this.multiRenderTarget.setSize(width, height);
            this.texelSize.set(1 / this.renderingManager.clientWidth, 1 / this.renderingManager.clientHeight);

            // SSAO simple render target is smaller for optimization
            this.ssaoRenderTarget.setSize(width, height);
            this.ssaoSimpleRenderTarget.setSize(this.renderingManager.clientWidth * 0.7, this.renderingManager.clientHeight * 0.7);
        }
    }
}
