import {
    WebGLRenderer, Scene, WebGLRenderTarget, Uniform, MeshBasicMaterial, LinearFilter, RGBFormat, Color, DoubleSide, ShaderMaterial, ShaderMaterialParameters,
    Points, BufferGeometry
} from "three";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { EffectPass } from "./EffectPass";
import { WVCamera } from "./WVCamera";
import { DisplayGroup } from "./DisplayGroup";
import { OutlineEffectMaterial } from "./OutlineEffectMaterial";
import { GeometryObject3D } from "../Model";
import {WVPointsMaterial} from "./WVPointsMaterial";

// language=GLSL
const fragment = `
varying vec2 vUv;    
    
uniform sampler2D edgeTexture;
uniform sampler2D maskTexture;

uniform vec3 color;
uniform float edgeStrength;
uniform float fillStrength;

void main() {
	float edge = texture2D(edgeTexture, vUv).r;
	float mask = texture2D(maskTexture, vUv).r;

	edge = clamp(edge * edgeStrength * mask, 0.0, 1.0);
	mask = (1.0 - mask) * fillStrength;
    gl_FragColor = vec4(color, edge + mask);
}
`;

export class SelectionEffectPass extends EffectPass {
    private selectionScene: Scene;
    private renderTargetMask: WebGLRenderTarget;
    private renderTargetOutline: WebGLRenderTarget;
    private maskPass: RenderPass;
    private outlinePass: EffectPass;
    private maskMaterial: MeshBasicMaterial;
    private shouldClear: boolean;

    constructor(camera: WVCamera, color: Color) {
        const material = new ShaderMaterial({
            uniforms: {},
            // language=GLSL
            vertexShader: `
                varying vec2 vUv;

                void main() {
                    vUv = uv;
                    gl_Position = vec4(position.xy, 0.0, 1.0);
                }
            `,
            fragmentShader: fragment,
            transparent: true,
            depthTest: false,
            depthWrite: false
        } as ShaderMaterialParameters);
        super(material);
        material.transparent = true;

        material.uniforms.maskTexture = new Uniform(null);
        material.uniforms.edgeTexture = new Uniform(null);
        material.uniforms.edgeStrength = new Uniform(5.0);
        material.uniforms.fillStrength = new Uniform(0.25);
        material.uniforms.color = new Uniform(color);

        this.selectionScene = new Scene();

        this.renderTargetMask = new WebGLRenderTarget(1, 1, {
            minFilter: LinearFilter,
            magFilter: LinearFilter
        });
        this.renderTargetMask.texture.generateMipmaps = false;
        this.renderTargetMask.texture.format = RGBFormat;
        this.getFullscreenMaterial().uniforms.maskTexture.value = this.renderTargetMask.texture;

        this.renderTargetOutline = new WebGLRenderTarget(1, 1, {
            minFilter: LinearFilter,
            magFilter: LinearFilter,
            stencilBuffer: false,
            depthBuffer: false,
            format: RGBFormat
        });
        this.renderTargetOutline.texture.generateMipmaps = false;
        this.getFullscreenMaterial().uniforms.edgeTexture.value = this.renderTargetOutline.texture;

        this.maskPass = new RenderPass(this.selectionScene, camera);
        this.maskPass.clearColor = new Color(0xffffff);
        this.maskPass.clearAlpha = 1;

        this.maskMaterial = new MeshBasicMaterial({color: 0x000000, side: DoubleSide});

        this.outlinePass = new EffectPass(new OutlineEffectMaterial());
        this.outlinePass.getFullscreenMaterial().uniforms.maskTexture.value = this.renderTargetMask.texture;

        this.shouldClear = true;
    }

    addObject(modelId: string, id: number | string, object: GeometryObject3D, groups: DisplayGroup[]= [{start: 0, count: Infinity}]): SelectionEffectPass {
        let cloned = this.selectionScene.getObjectByName(modelId + id.toString()) as GeometryObject3D;
        if (!cloned) {
            cloned = object.clone(false) as GeometryObject3D;
            cloned.name = modelId + id.toString();
            cloned.matrixAutoUpdate = false;
            cloned.matrix = object.matrixWorld;
            cloned.geometry = new BufferGeometry();

            // TODO: move this logic into respective object classes
            if (object instanceof Points) {
                // @ts-ignore
                const m = object.material[0].clone() as WVPointsMaterial;
                m.opacity = 1;
                m.transparent = false;
                cloned.material = [m];
            }
            else {
                cloned.material = [this.maskMaterial];
            }
            this.selectionScene.add(cloned);
        }

        const geom = cloned.geometry;
        const g = object.geometry;
        geom.boundingBox = g.boundingBox;
        geom.boundingSphere = g.boundingSphere;
        geom.attributes = g.attributes;
        geom.setIndex(g.getIndex());
        geom.clearGroups();
        for (const group of groups)
            geom.addGroup(group.start, group.count, 0);
        return this;
    }

    removeObject(modelId: string, id: number | string): SelectionEffectPass {
        const cloned = this.selectionScene.getObjectByName(modelId + id.toString());
        this.selectionScene.remove(cloned);
        if (this.selectionScene.children.length === 0)
            this.shouldClear = true;
        return this;
    }

    render(renderer: WebGLRenderer, writeBuffer: WebGLRenderTarget): void {
        if (this.selectionScene.children.length > 0 || this.shouldClear) {
            this.maskPass.render(renderer, undefined, this.renderTargetMask, undefined, undefined);
            this.outlinePass.render(renderer, this.renderTargetOutline);
            super.render(renderer, writeBuffer);
            this.shouldClear = false;
        }
    }

    setSize(width: number, height: number): void {
        this.renderTargetMask.setSize(width, height);

        // 0.9 adds a bit of free blur
        const w = width / window.devicePixelRatio * 0.9;
        const h = height / window.devicePixelRatio * 0.9;
        this.maskPass.setSize(w, h);
        this.renderTargetOutline.setSize(w, h);
        (this.outlinePass.getFullscreenMaterial() as OutlineEffectMaterial).setTexelSize(2.4 / width * window.devicePixelRatio, 2.4 / height * window.devicePixelRatio);
        this.shouldClear = true;
    }
}
