import {
    Color,
    CubeTexture,
    Euler,
    Matrix4,
    Scene,
    Texture,
    Uniform,
    WebGLCubeRenderTarget,
    BackSide,
    FrontSide,
    Mesh,
    BoxBufferGeometry,
    ShaderMaterial,
    ShaderLib,
    PlaneBufferGeometry,
    PerspectiveCamera, RepeatWrapping, MathUtils
} from "three";
import {cloneUniforms} from "three/src/renderers/shaders/UniformsUtils";
import {RenderingManager} from "./RenderingManager";
import {BackgroundGradient, BackgroundSetting, Settings} from "../common";
import {WVCamera} from "./WVCamera";
import {ToneMapping} from "three";
import {EquirectangularToCubeGenerator} from "./EquirectangularToCubeGenerator";
import {SettingsProxyHandler} from "../SettingsProxyHandler";

export class WVBackground {
    private background: Color | Texture | CubeTexture | WebGLCubeRenderTarget;
    private currentBackground: Color | Texture | CubeTexture | WebGLCubeRenderTarget;

    private boxMesh: Mesh;
    private planeMesh: Mesh;
    private currentBackgroundVersion = 0;
    private currentTonemapping: ToneMapping;

    private backgroundEuler = new Euler();
    private backgroundScene = new Scene();
    private backgroundCamera = new PerspectiveCamera();

    constructor(private renderingManager: RenderingManager, private settingsProxy: SettingsProxyHandler<Settings>, private camera: WVCamera) {
        this.update();
        this.settingsProxy.addListener("background", () => {
            this.update();
            this.renderingManager.redraw();
        });

        this.settingsProxy.addListener("backgroundRotation", () => {
            this.renderingManager.redraw();
        });
    }

    private syncCamera(): void {
        this.backgroundEuler.x = -Math.PI / 2;
        this.backgroundEuler.z = this.settingsProxy.settings.backgroundRotation * Math.PI / 180;
        this.backgroundCamera.quaternion.setFromEuler(this.backgroundEuler);
        this.backgroundCamera.quaternion.multiply(this.camera.quaternion);
        if (
            this.backgroundCamera.aspect !== this.camera.aspect ||
            this.backgroundCamera.fov !== this.camera.fov
        ) {
            this.backgroundCamera.aspect = this.camera.aspect;
            this.backgroundCamera.fov = this.camera.fov;
            this.backgroundCamera.updateProjectionMatrix();
        }
    }

    private update(): void {
        this.background =
            this.toColor(this.settingsProxy.settings.background) ||
            this.toGradient(this.settingsProxy.settings.background) ||
            this.toSkybox(this.settingsProxy.settings.background) ||
            this.toTexture(this.settingsProxy.settings.background);
    }

    private toGradient(background: BackgroundSetting): Texture {
        const gradient = background as BackgroundGradient;
        if (gradient.topColor && gradient.bottomColor) {
            const canvas = document.createElement("canvas");
            canvas.width = 1;
            canvas.height = 2;
            const context = canvas.getContext("2d");
            context.fillStyle = "#" + gradient.topColor.getHexString();
            context.fillRect(0, 0, 1, 1);
            context.fillStyle = "#" + gradient.bottomColor.getHexString();
            context.fillRect(0, 1, 1, 1);
            const texture = this.toTexture(canvas);

            // crop half pixel on top and bottom for better vertical gradients
            texture.repeat.y = (canvas.height - 1) / canvas.height;
            texture.offset.y = 0.5 / canvas.height;
            return texture;
        }

    }

    private toColor(background: BackgroundSetting): Color {
        const color = background as Color;
        return color.r !== undefined && color.g !== undefined && color.b !== undefined ? color : undefined;
    }

    private toTexture(background: BackgroundSetting): Texture {
        if (background instanceof HTMLImageElement || background instanceof HTMLCanvasElement) {
            const texture = new Texture(background);
            texture.needsUpdate = true;
            return texture;
        }
    }

    private toSkybox(background: BackgroundSetting): CubeTexture | WebGLCubeRenderTarget {
        if (background instanceof HTMLImageElement || background instanceof HTMLCanvasElement) {
            if (background.height * 4 === background.width * 3) {
                const cubeTexture = new CubeTexture(this.getImagesFromSkyboxAtlas(background));
                cubeTexture.needsUpdate = true;
                return cubeTexture;
            }
            else if (background.height * 2 === background.width) {
                const textureSrc = new Texture(background);
                textureSrc.wrapS = RepeatWrapping;
                textureSrc.needsUpdate = true;
                const equiToCube = new EquirectangularToCubeGenerator(textureSrc, { resolution: MathUtils.ceilPowerOfTwo(background.height * 0.6) });
                return equiToCube.update(this.renderingManager.renderer);
            }
        }
        else if (background instanceof Array) {
            const cubeTexture = new CubeTexture(background);
            cubeTexture.needsUpdate = true;
            return cubeTexture;
        }
    }

    private getImagesFromSkyboxAtlas(srcImg: HTMLImageElement | HTMLCanvasElement): HTMLCanvasElement[] {
        const imgs: HTMLCanvasElement[] = [];
        const imageObj = srcImg;
        const size = imageObj.height / 3;
        for (let i = 0; i < 6; i++) {
            const canvas = document.createElement('canvas');
            const context = canvas.getContext('2d');
            canvas.height = size;
            canvas.width = size;
            if (i === 0) context.drawImage(imageObj, size * 2, size, size, size, 0, 0, size, size);
            if (i === 1) context.drawImage(imageObj, 0, size * i, size, size, 0, 0, size, size);
            if (i === 2) context.drawImage(imageObj, size, 0, size, size, 0, 0, size, size);
            if (i === 3) context.drawImage(imageObj, size, size * 2, size, size, 0, 0, size, size);
            if (i === 4) context.drawImage(imageObj, size, size, size, size, 0, 0, size, size);
            if (i === 5) context.drawImage(imageObj, size * 3, size, size, size, 0, 0, size, size);
            imgs.push(canvas);
        }
        return imgs;
    }

    render(): void {
        const renderer = this.renderingManager.renderer;

        // Ignore background in AR
        const xr = renderer.xr;
        const session = xr.getSession && xr.getSession();
        if (session && session.environmentBlendMode === 'additive') {
            this.background = null;
        }

        if (this.background === null) {
            return;
        } else if (this.background && this.background instanceof Color) {
            renderer.setClearColor(this.background);
            renderer.setClearAlpha(1);
            renderer.clear();
            return;
        }

        if (this.background && (this.background instanceof CubeTexture || this.background instanceof WebGLCubeRenderTarget)) {

            if (this.boxMesh === undefined) {

                const uniforms = cloneUniforms(ShaderLib.cube.uniforms);
                uniforms.rotMatrix = new Uniform(new Matrix4());

                const backgroundMaterial = new ShaderMaterial({
                    uniforms: uniforms,
                    vertexShader: ShaderLib.cube.vertexShader,
                    fragmentShader: ShaderLib.cube.fragmentShader,
                    side: BackSide,
                    depthTest: false,
                    depthWrite: false,
                    fog: false
                });

                const boxGeometry = new BoxBufferGeometry(100, 100, 100);
                this.boxMesh = new Mesh(boxGeometry, backgroundMaterial);

                boxGeometry.deleteAttribute('normal');
                boxGeometry.deleteAttribute('uv');

                // enable code injection for non-built-in material
                Object.defineProperty(this.boxMesh.material, 'envMap', {
                    get: function () {
                        return this.uniforms.envMap.value;
                    }
                });
                this.backgroundScene.children.length = 0;
                this.backgroundScene.add(this.boxMesh);
            }

            if (this.planeMesh) {
                this.backgroundScene.remove(this.planeMesh);
                this.planeMesh = undefined;
            }

            const texture = this.background instanceof WebGLCubeRenderTarget ? this.background.texture : this.background;

            // @ts-ignore
            this.boxMesh.material.uniforms.envMap.value = texture;
            // @ts-ignore
            this.boxMesh.material.uniforms.flipEnvMap.value = texture.isCubeTexture ? -1 : 1;

            if (this.currentBackground !== this.background ||
                this.currentBackgroundVersion !== texture.version ||
                this.currentTonemapping !== renderer.toneMapping) {

                // @ts-ignore
                this.boxMesh.material.needsUpdate = true;

                this.currentBackground = this.background;
                this.currentBackgroundVersion = texture.version;
                this.currentTonemapping = renderer.toneMapping;
            }

        } else if (this.background && this.background instanceof Texture) {

            if (this.planeMesh === undefined) {
                const planeGeometry = new PlaneBufferGeometry(2, 2);
                this.planeMesh = new Mesh(
                    planeGeometry,
                    new ShaderMaterial({
                        uniforms: cloneUniforms(ShaderLib.background.uniforms),
                        vertexShader: ShaderLib.background.vertexShader,
                        fragmentShader: ShaderLib.background.fragmentShader,
                        side: FrontSide,
                        depthTest: false,
                        depthWrite: false,
                        fog: false
                    })
                );
                planeGeometry.deleteAttribute('normal');

                // enable code injection for non-built-in material
                Object.defineProperty(this.planeMesh.material, 'map', {
                    get: function () {
                        return this.uniforms.t2D.value;
                    }
                });

                this.backgroundScene.children.length = 0;
                this.backgroundScene.add(this.planeMesh);
            }

            if (this.boxMesh) {
                this.backgroundScene.remove(this.boxMesh);
                this.boxMesh = undefined;
            }

            // @ts-ignore
            this.planeMesh.material.uniforms.t2D.value = this.background;


            if (this.background.matrixAutoUpdate === true) {
                // @ts-ignore
                this.background.updateMatrix();
            }

            // @ts-ignore
            this.planeMesh.material.uniforms.uvTransform.value.copy(this.background.matrix);

            if (this.currentBackground !== this.background ||
                this.currentBackgroundVersion !== this.background.version ||
                this.currentTonemapping !== renderer.toneMapping) {

                // @ts-ignore
                this.planeMesh.material.needsUpdate = true;

                this.currentBackground = this.background;
                this.currentBackgroundVersion = this.background.version;
                this.currentTonemapping = renderer.toneMapping;

            }
        }

        this.syncCamera();
        renderer.render(this.backgroundScene, this.backgroundCamera);
    }
}
