import {Box3, Material, Uniform, Vector2, WebGLRendererParameters} from "three";
import {Api} from "../Api";
import {WVCamera} from "./WVCamera";
import {VRManager} from "./VRManager";
import isMobile from "ismobilejs";
import {GeometryObject3D} from "../Model";
import {WebGLRenderer} from "./WebGLRenderer";
import {iterate} from "../Helpers/common-utils";
import {PassComposer} from "./PassComposer";
import {WebglViewerEventName} from "../common";

export interface GlobalMaterialUniforms {
    viewSize: Uniform;
    pixelRatio: Uniform;
    globalOpacity: Uniform;
    globalTransparent?: boolean;
    depthPeelingEnabled: Uniform;
    peelingDepthTexture: Uniform;
    depthPrecisionFix: Uniform;
}

export class RenderingManager {
    enabled: boolean;
    composer: PassComposer;
    renderer: WebGLRenderer;
    private renderRequested: boolean;
    private beforeRenderListeners: Array<() => void> = [];
    private afterRenderListeners: Array<() => void> = [];
    private animationFrameListeners: Array<(timestamp: number) => void> = [];
    vr: VRManager;
    private _isMobile: boolean;
    private driverRendererString: string;
    private lastRenderTime: number;

    uniforms: GlobalMaterialUniforms = {
        viewSize: new Uniform(new Vector2(this.width, this.height)),
        pixelRatio: new Uniform(devicePixelRatio),
        globalOpacity: new Uniform(1),
        depthPeelingEnabled: new Uniform(Number(false)),
        peelingDepthTexture: new Uniform(null),
        depthPrecisionFix: new Uniform(0)
    };

    get fullRender(): boolean {
        // rendering fully on render request idle (progressive rendering)
        return !this.renderRequested;
    }

    private readonly _camera: WVCamera;
    private _boundingBox: Box3;

    constructor(private _api: Api) {
        this.renderRequested = true;
        this._camera = _api.camera;
        this.composer = this.initializeRenderer();

        this._api.camera.subscribe(() => {
            if (this._boundingBox && !this._boundingBox.isEmpty() && !this.vr.enabled)
                this._camera.fitNearAndFarPlanes(this._boundingBox);
            this.redraw();
        });
        this._api.models.worldBoundingBox.subscribe((aabb: Box3) => {
            if (!aabb.isEmpty() && !this.vr.enabled) {
                this._boundingBox = aabb;
                this._camera.fitNearAndFarPlanes(this._boundingBox);
                this.redraw();
            }
        });
        this._api.settingsProxy.addListener("darkModeMaterials", () => this.redraw());
        this._api.settingsProxy.addListener("renderEdges", () => this.redraw());
        this._api.settingsProxy.addListener("vertexInterpolationMaterials", () => this.redraw());
    }

    isMobile(): boolean {
        if (this._isMobile === undefined) this._isMobile = isMobile(navigator).any;
        return this._isMobile;
    }

    isWebgl2(): boolean {
        return this.renderer.capabilities.isWebGL2;
    }

    isFragDepthAvailable(): boolean {
        return this.isWebgl2() || !!this.renderer.extensions.get("EXT_frag_depth");
    }

    isDepthTextureAvailable(): boolean {
        return this.isWebgl2() || !!this.renderer.extensions.get("WEBGL_depth_texture");
    }

    isHalfFloatTextureTypeAvailable(): boolean {
        return this.isWebgl2() || !!this.renderer.extensions.get("OES_texture_half_float");
    }

    get globalOpacity(): number {
        return this.composer.depthPeelingRenderPass.globalOpacity;
    }

    set globalOpacity(value: number) {
        this.composer.depthPeelingRenderPass.globalOpacity = value;
        this.redraw();
    }

    redraw(): void {
        this.renderRequested = true;
    }

    addBeforeRenderListener(listener: () => void): void {
        this.beforeRenderListeners.push(listener);
    }

    addAfterRenderListener(listener: () => void): void {
        this.afterRenderListeners.push(listener);
    }

    private removeListener(listeners: any[], listener: any): void {
        const index = listeners.indexOf(listener);
        if (index !== -1) listeners.splice(index, 1);
    }

    removeBeforeRenderListener(listener: () => void): void {
        this.removeListener(this.beforeRenderListeners, listener);
    }

    removeAfterRenderListener(listener: () => void): void {
        this.removeListener(this.afterRenderListeners, listener);
    }

    addAnimationFrameListener(listener: (timestamp: number) => void): void {
        this.animationFrameListeners.push(listener);
    }

    removeAnimationFrameListener(listener: (timestamp: number) => void): void {
        this.removeListener(this.animationFrameListeners, listener);
    }

    start(): void {
        this.renderer.setAnimationLoop((t: number) => this.animationLoop(t));
    }

    private animationLoop(timestamp: number): void {
        this.updateCanvasSize();

        if (this.enabled) {
            if (this.renderRequested) {
                this.render();
                this.lastRenderTime = timestamp;
                this.renderRequested = false;
            }
            // full render on render request idle (progressive rendering)
            else if (this.lastRenderTime && timestamp - this.lastRenderTime > 100) {
                this.render();
                this.lastRenderTime = undefined;
            }
        }
        this.animationFrameListeners.forEach(l => l(timestamp));
    }

    private render(): void {
        this.beforeRenderListeners.forEach(l => l());
        this.composer.render();
        this.afterRenderListeners.forEach(l => l());
    }

    async screenshot(): Promise<Blob> {
        this.composer.render();
        return await new Promise((resolve: BlobCallback) => {
            this.renderer.domElement.toBlob(resolve);
        });
    }

    private updateCanvasSize(): void {
        if (this.vr.enabled) return;

        const canvas = this.renderer.domElement;
        if ((Math.abs(canvas.height - this.height) > 1 || Math.abs(canvas.width - this.width) > 1) && this.clientWidth > 10 && this.clientHeight > 10) {
            this.renderer.setPixelRatio(devicePixelRatio);
            this.uniforms.pixelRatio.value = devicePixelRatio;
            this.uniforms.viewSize.value.x = this.width;
            this.uniforms.viewSize.value.y = this.height;

            this._camera.aspect = canvas.clientWidth / canvas.clientHeight;
            this.composer.setSize(this.width, this.height);
            this.renderer.setSize(this.clientWidth, this.clientHeight, false);
            this._camera.updateProjectionMatrix();
            this._camera.callListeners();
        }
    }

    initializeRenderer(): PassComposer {
        this.enabled = false;

        const canvas = this._api.container.children[0] as HTMLCanvasElement;

        const contextAttributes = {
            stencil: false,
            alpha: false,
            powerPreference: "high-performance",
            antialias: this._api.settingsProxy.settings.xrCompatible, // force MSAA for xr devices only, FXAA will be used for others
            xrCompatible: this._api.settingsProxy.settings.xrCompatible
        } as WebGLContextAttributes;
        let context: WebGLRenderingContext;
        if (this._api.settingsProxy.settings.useWebgl2)
            context = canvas.getContext("webgl2", contextAttributes) as WebGLRenderingContext;
        if (!context)
            context = canvas.getContext("webgl", contextAttributes) as WebGLRenderingContext;

        const params = { canvas: canvas, context: context } as WebGLRendererParameters;
        this.renderer = new WebGLRenderer(params);

        const gl = this.renderer.getContext();
        this.driverRendererString = gl.getParameter(gl.getExtension("WEBGL_debug_renderer_info").UNMASKED_RENDERER_WEBGL);
        // tslint:disable-next-line:no-console
        console.log(`Rendering: ${gl.getParameter(gl.VERSION)}, ${gl.getParameter(gl.SHADING_LANGUAGE_VERSION)}, ${gl.getParameter(gl.VENDOR)}, ${this.driverRendererString}`);

        this.vr = new VRManager(this._api);
        this.updateCanvasSize();
        this.enabled = true;

        return new PassComposer(this._api, this);
    }

    traverseMaterials(callback: (o: GeometryObject3D, m: Material) => void): void {
        this._api.scene.traverse(o => {
            const obj = o as GeometryObject3D;
            if (obj.material)
                iterate(obj.material, m => callback(obj, m));
        });
    }

    private get viewportScale(): number {
        // @ts-ignore
        return window.visualViewport ? window.visualViewport.scale : 1;
    }

    get width(): number {
        return Math.trunc(this._api.container.clientWidth * devicePixelRatio * this.viewportScale);
    }

    get height(): number {
        return Math.trunc(this._api.container.clientHeight * devicePixelRatio * this.viewportScale);
    }

    get clientWidth(): number {
        return Math.trunc(this._api.container.clientWidth * this.viewportScale);
    }

    get clientHeight(): number {
        return Math.trunc(this._api.container.clientHeight * this.viewportScale);
    }
}
