import { Vector3, Box3, Quaternion, Euler } from "three";
import { WVCamera } from "./Rendering/WVCamera";
import {Settings, ProjectionType, ViewPreset} from "./common";
import { Models } from "./Models";
import { Selection } from "./Selection";
import {easeInOutQuad} from "./Animation";
import {InputHandler} from "./InputHandler";
import {SettingsProxyHandler} from "./SettingsProxyHandler";

export { easeLinear, easeInOutQuad, easeInQuad, easeOutQuad } from "./Animation"

export interface Fit {
    // modelId -> entityIds
    models?: Map<string, number[]>;
    viewPreset?: ViewPreset;
    tightness?: number;
}

export interface TrimbleRotation {
    pitch: number;
    yaw: number;
}

export class PublicCamera {
    private _rotation: TrimbleRotation = {pitch: 0, yaw: 0};
    private _direction = new Vector3();
    private _tmpVec3 = new Vector3();
    private _tmpQuat= new Quaternion();
    private _tmpEuler= new Euler();

    constructor(private _camera: WVCamera, private _models: Models, private _settingsHandler: SettingsProxyHandler<Settings>) {
    }

    subscribeKeys(inputHandler: InputHandler, selection: Selection): void {
        inputHandler.keyDown$.subscribe((e) => {
            if (e.code === "KeyF") this.fitToView({models: selection.size ? selection : undefined});
        });
    }

    get position(): Vector3 {
        return this._camera.position;
    }

    set position(p: Vector3) {
        this._camera.position.copy(p);
        this._camera.callListeners();
    }

    get quaternion(): Quaternion {
        return this._camera.quaternion;
    }

    set quaternion(q: Quaternion) {
        this._camera.quaternion.copy(q);
        this._camera.callListeners();
    }

    get nearMin(): number {
        return this._camera.nearMin;
    }

    set nearMin(nearMin: number) {
        this._camera.nearMin = nearMin;
    }

    get rotation(): TrimbleRotation {
        const forward = this._tmpVec3.set(0, 0, -1).applyQuaternion(this._camera.quaternion);
        this._rotation.pitch = Math.PI - Math.atan2(Math.sqrt(forward.x*forward.x + forward.y*forward.y), forward.z);

        if (Math.abs(forward.z) > 0.999) this._tmpVec3.set(0, 1, 0).applyQuaternion(this._camera.quaternion); // up
        this._rotation.yaw = -Math.atan2(this._tmpVec3.x, this._tmpVec3.y);
        return this._rotation;
    }

    set rotation(r: TrimbleRotation) {
        this.quaternion = this.rotationToQuaternion(r);
        this._camera.callListeners();
    }

    private rotationToQuaternion(r: TrimbleRotation): Quaternion {
        let yaw = r.yaw;
        if (Math.abs(r.pitch - Math.PI) < 0.999) yaw += Math.PI;
        this._tmpEuler.set(r.pitch, 0, yaw, "YZX");
        return this._tmpQuat.setFromEuler(this._tmpEuler);
    }

    get direction(): Vector3 {
        return this._camera.getWorldDirection(this._direction);
    }

    get projectionType(): ProjectionType {
        return this._camera.getProjectionType();
    }

    set projectionType(t: ProjectionType) {
        this._camera.setProjectionType(t);
    }

    // not a duplicate, this one returns a promise
    setProjectionType(t: ProjectionType): Promise<void> {
        return this._camera.setProjectionType(t);
    }

    get aspect(): number {
        return this._camera.aspect;
    }

    // Angle of perspective view in degrees of wider aspect ratio (might be horizontal or vertical)
    get fieldOfView(): number {
        return this._camera.fov;
    }

    set fieldOfView(fov: number) {
        this._camera.fov = fov;
        this._camera.callListeners();
    }

    // Orthographic view size in meters of wider aspect ratio (might be horizontal or vertical)
    get orthoSize(): number {
        return this._camera.orthoSize;
    }

    set orthoSize(orthoSize: number) {
        this._camera.orthoSize = orthoSize;
        this._camera.callListeners();
    }

    async animate(position?: Vector3, quaternion?: Quaternion, rotation?: TrimbleRotation, animationTime?: number, easeFunc?: (t: number) => number): Promise<void> {
        if (rotation) quaternion = this.rotationToQuaternion(rotation);
        await this._camera.animate(position, quaternion, animationTime, easeFunc);
    }

    async fitToView(fit?: Fit | string): Promise<void> {
        if (typeof fit === "string")
            fit = {models: new Map([[fit, undefined]])};

        if  (fit === undefined || fit.models === undefined) {
            await this._camera.fitToView(this._models.worldBoundingBox.value, this._settingsHandler.settings.animationTime, fit ? fit.viewPreset : undefined, fit ? fit.tightness : undefined);
            return;
        }

        const box = new Box3();
        for (const [modelId, entityIds] of fit.models) {
            const model = this._models.get(modelId);
            if (!model) throw new Error(`No model found named ${modelId}`);
            box.union(await model.getBoundingBox(entityIds));
        }
        await this._camera.fitToView(box, this._settingsHandler.settings.animationTime, fit.viewPreset, fit.tightness) ;
    }
}
