import { DirectionToSpherical } from "../Helpers/utils";
import {
    InputHandler,
    PointerInput,
} from "../InputHandler";
import { Vector3, Vector2, Box3 } from "three";
import { BehaviorSubject, Observable, Subscription } from "rxjs";
import { WVCamera } from "../Rendering/WVCamera";
import { MouseButton } from "../common";
import { SinglePointerTool } from "./SinglePointerTool";
import {Vector3Const} from "../Helpers/common-utils";

export abstract class AbstractOrbit extends SinglePointerTool {
    protected EPS = 0.02;

    protected _camera: WVCamera;
    private _dragHandle: Subscription;
    private dragObservable: Observable<PointerInput>;
    private _offset = new Vector3();

    protected _originalPosition: Vector3;
    protected _originalDirection: Vector3;
    protected _originalUp: Vector3;
    protected _rotationPoint: Vector3;
    protected calculatingRotationPoint: boolean;

    protected _startPoint: Vector2;

    abstract name: string;

    protected constructor(protected inputs: InputHandler, camera: WVCamera) {
        super();
        this.mouseButton = MouseButton.left;
        this.touchCount = 1;

        this._camera = camera;

        this.dragObservable = inputs.createDragObservable(this.observableOptions,
            e => this.downCallback(e),
            e => this.moveCallback(e),
            e => this.upCallback(e));
    }

    set enabled(enabled: boolean) {
        if (this._dragHandle) {
            this._dragHandle.unsubscribe();
            this._dragHandle = null;
        }
        if (!enabled) return;

        this._dragHandle = this.dragObservable.subscribe();
    }

    get enabled(): boolean {
        return !!this._dragHandle;
    }

    protected async downCallback(event: PointerInput): Promise<PointerInput> {
        this._startPoint = new Vector2(event.x, event.y);
        this._rotationPoint = undefined;
        return event;
    }

    protected async moveCallback(event: PointerInput): Promise<PointerInput> {
        if (this.calculatingRotationPoint) return;
        if (!this._rotationPoint) {
            this.calculatingRotationPoint = true;
            await this.calculateRotationPoint(event);
            this.calculatingRotationPoint = false;
        }

        const delta = new Vector2(event.x, event.y).sub(this._startPoint);
        const sphericalDirection = DirectionToSpherical(
            this._originalDirection,
            this._originalUp
        );
        this.translateCamera(delta, sphericalDirection);
        this._camera.callListeners();
        return event;
    }

    protected abstract upCallback(event: PointerInput): PointerInput;

    protected calculateRotationPoint(event: PointerInput): void {
        this.updateOriginalCamera();
    }

    protected translateCamera(delta: Vector2, sphericalDirection: Vector2): void {
        sphericalDirection.x = Math.max(this.EPS, Math.min(sphericalDirection.x + delta.y * 0.007, Math.PI - this.EPS));
        sphericalDirection.y -= delta.x * 0.003;
        this._camera.rotateSpherical(this._rotationPoint, this._offset, sphericalDirection);
    }

    updateOriginalCamera = (() => {
        this._originalUp = new Vector3();
        const right = new Vector3();

        return () => {
            this._originalDirection = this._camera.getWorldDirection(new Vector3());
            this._originalPosition = this._camera.position.clone();

            this._originalUp.copy(Vector3Const.forward);
            right.copy(Vector3Const.right);

            this._originalUp.applyQuaternion(this._camera.quaternion);
            right.applyQuaternion(this._camera.quaternion);

            const offset = new Vector3()
                .copy(this._rotationPoint)
                .sub(this._originalPosition);
            const direction = this._originalDirection;

            this._offset.set(offset.dot(right), offset.dot(direction), offset.dot(this._originalUp));
        };
    })();
}

export class Orbit extends AbstractOrbit {
    static get Name(): string { return "orbit" };
    useWorldCenter: boolean = false;

    get name(): string {
        return Orbit.Name;
    }

    constructor(inputs: InputHandler, camera: WVCamera, private _worldBoundingBox: BehaviorSubject<Box3>) {
        super(inputs, camera);
        this.enabled = true;
    }


    protected async calculateRotationPoint(event: PointerInput): Promise<void> {
        if (!this.useWorldCenter)
            event.intersection = await this.inputs.picker.pickForNavigation(new Vector2(event.x, event.y));
        if (!this.useWorldCenter && event.intersection && event.intersection.point) {
            this._rotationPoint = event.intersection.point;
        } else {
            this._rotationPoint = this._worldBoundingBox.value.getCenter(new Vector3());
        }
        super.calculateRotationPoint(event);
    }

    public upCallback(event: PointerInput): PointerInput {
        return event;
    }
}
