import {PickEventDetail, SelectionEventDetail, Tool, WebglViewerEventName} from "./common";

export class EventDispatcher {
    constructor(private _element: HTMLElement) {}

    public error(message: string): void {
        const log = {
            detail: message,
            bubbles: true,
            composed: true
        };
        console.error(message);
        this._element.dispatchEvent(new CustomEvent(WebglViewerEventName.Error, log));
    }

    public selected(detail: SelectionEventDetail): void {
        const event = new CustomEvent<SelectionEventDetail>(WebglViewerEventName.Selection, {
            detail: detail,
            bubbles: true,
            composed: true
        });
        this._element.dispatchEvent(event);
    }

    public picked(detail: PickEventDetail): void {
        const event = new CustomEvent<PickEventDetail>(WebglViewerEventName.Pick, {
            detail: detail,
            bubbles: true,
            composed: true
        });
        this._element.dispatchEvent(event);
    }

    public navigation(start: boolean): void {
        const event = new CustomEvent(start ? WebglViewerEventName.NavigationStart : WebglViewerEventName.NavigationEnd, {
            bubbles: true,
            composed: true
        });
        this._element.dispatchEvent(event);
    }

    public dragStart(tool: Tool): void {
        const event = new CustomEvent(WebglViewerEventName.DragStart, {
            detail: tool,
            bubbles: true,
            composed: true,
        });
        this._element.dispatchEvent(event);
    }

    public dragEnd(tool: Tool): void {
        const event = new CustomEvent(WebglViewerEventName.DragEnd, {
            detail: tool,
            bubbles: true,
            composed: true
        });
        this._element.dispatchEvent(event);
    }

    public dispatch(event: CustomEvent): void {
        this._element.dispatchEvent(event);
    }

    public subscribe(eventName: WebglViewerEventName, listener: (e: CustomEvent) => void): void {
        this._element.addEventListener(eventName, listener as EventListener);
    }
}
