import {ToolManager} from "./Tools/ToolManager";
import {RenderingManager} from "./Rendering/RenderingManager";
import {Picker} from "./Picker/Picker";
import {EventDispatcher} from "./EventDispatcher";
import {HtmlElementPositioner} from "./HtmlElementPositioner";
import {Models} from "./Models";
import {Registry} from "./Registry";
import {InputHandler} from "./InputHandler";

import {Settings, LengthUnit, LoadOptions, QualityPreset, Tool} from "./common";
import {Color, Object3D, Scene} from "three";
import {Api} from "./Api";
import {Selection} from "./Selection";

import {WVCamera} from "./Rendering/WVCamera";
import {QualityAwarePlugin, WebglViewerPlugin} from "./WebglViewerPlugin";
import {ApiContainer} from "./ApiContainer";
import {VRManager} from "./Rendering/VRManager";
import {Model} from "./Model";
import {version} from "./Version";
import {Cursor3D} from "./Picker/Cursor3D";
import {Analytics} from "./Analytics";
import {PublicCamera} from "./PublicCamera";
import { TextureGenerator } from "./Helpers/TextureGenerator";
import {Vector3Const} from "./Helpers/common-utils";
import {SettingsProxyHandler} from "./SettingsProxyHandler";

export class WebglViewer extends HTMLElement {
    private _api: Api;
    private _publicCamera: PublicCamera;

    constructor(settings?: Settings) {
        super();
        // tslint:disable-next-line:no-console
        console.log(`webgl-viewer build version: ${version}`);

        const settingsProxy = new SettingsProxyHandler<Settings>(Object.assign({
            background: {topColor: new Color("#FFFFFF"), bottomColor: new Color("#AAAAAA")},
            color: new Color("#000000"),
            lengthUnit: LengthUnit.m,
            decimals: 2,
            backgroundRotation: 0,
            darkModeMaterials: false,
            vertexInterpolationMaterials: false,
            progressiveRendering: true,
            renderEdges: true,
            ssao: true,
            orderIndependentTransparency: true,
            transparencyPeelsCount: 2,
            hoverHighlightEnabled: false,
            antialias: undefined, // undefined === automatic
            xrCompatible: false,
            useWebgl2: true,
            animationTime: 500,
            selectionColor: new Color("#ffcc00"),
            hoverColor: new Color("#0000ff"),
            staticRootUrl: undefined,
            snapDistance: 12,
            analyticsEnabled: true,
            sandbox: false
        } as Settings, settings || {}));

        this._initializeApi(settingsProxy);
        this._api.analytics.addViewerErrorStackKeyword(this.constructor.name);
    }

    get settings(): Settings {
        return this._api.settingsProxy.settings;
    }

    activateQualityPreset(quality: QualityPreset): void {
        if (quality === QualityPreset.HighQuality) {
            this.settings.progressiveRendering = false;
            this.settings.antialias = true;
            this.settings.renderEdges = true;
            this.settings.ssao = true;
            this.settings.orderIndependentTransparency = true;
            this.settings.transparencyPeelsCount = 4;
            this.settings.vertexInterpolationMaterials = false;
        }
        else if (quality === QualityPreset.Optimal) {
            this.settings.progressiveRendering = true;
            this.settings.antialias = undefined;
            this.settings.renderEdges = true;
            this.settings.ssao = true;
            this.settings.orderIndependentTransparency = true;
            this.settings.transparencyPeelsCount = 2;
            this.settings.vertexInterpolationMaterials = false;
        }
        else if (quality === QualityPreset.HighPerformance) {
            this.settings.progressiveRendering = true;
            this.settings.antialias = undefined;
            this.settings.renderEdges = true;
            this.settings.ssao = true;
            this.settings.orderIndependentTransparency = true;
            this.settings.transparencyPeelsCount = 1;
            this.settings.vertexInterpolationMaterials = false;
        }
        else if (quality === QualityPreset.MaxPerformance) {
            this.settings.progressiveRendering = true;
            this.settings.antialias = false;
            this.settings.renderEdges = false;
            this.settings.ssao = false;
            this.settings.orderIndependentTransparency = false;
            this.settings.vertexInterpolationMaterials = true;
        }

        for (const p in this.plugins) {
            const plugin = this.plugins[p] as any as QualityAwarePlugin;
            if (plugin.activateQualityPreset) plugin.activateQualityPreset(quality);
        }
    }

    get globalOpacity(): number {
        return this._api.renderingManager.globalOpacity;
    }

    set globalOpacity(value: number) {
        this._api.renderingManager.globalOpacity = value;
    }

    addPlugin(p: WebglViewerPlugin): WebglViewer {
        const plugin = p as WebglViewerPlugin;
        plugin.api = this._api;
        if (this.plugins[plugin.name]) throw Error(`Plugin ${plugin.name} is already initialized`);
        if (plugin.version !== version) throw Error(`Plugin version mismatch ${plugin.version}`);
        this.plugins[plugin.name] = plugin;

        this._api.analytics.addViewerErrorStackKeyword(plugin.constructor.name);
        return this;
    }

    async load(ref: any, options: LoadOptions = {}): Promise<Model> {
        options.fitToView = options.fitToView !== undefined ? options.fitToView : true;

        const loader = Array.from(this._api.registry.LUT.entries()).find(pair => pair[0](ref, options));
        if (!loader)
            throw new Error(`Unknown model type: ${ref}`);

        const model = await loader[1](ref, options);

        if (options.transform) model.transform(options.transform);
        if (options.fitToView) {
            const box = this._api.models.worldBoundingBox.value;
            this._api.camera.fitToView(box, this._api.settingsProxy.settings.animationTime);
        }

        this._api.renderingManager.redraw();
        this._api.analytics.logLoadEvent(model.name);
        return model;
    }

    async unload(modelId: string, options?: LoadOptions): Promise<void> {
        options = options || { fitToView: true };
        modelId = options.modelId || modelId;

        await this._api.models.remove(modelId);
        if (options.fitToView)
            this._api.camera.fitToView(this._api.models.worldBoundingBox.value, this._api.settingsProxy.settings.animationTime);
        else
            this._api.renderingManager.redraw();
    }

    getModel<T extends Model>(modelId: string, type?: new (...params: any) => T): T {
        const model = this._api.models.get(modelId);
        return type === undefined || model instanceof type ? model as T : undefined;
    }

    getModels(): Model[] {
        return Array.from(this._api.models.getIterable());
    }

    private _initializeApi(settingsProxy: SettingsProxyHandler<Settings>): void {
        this.initApiForJsdom();
        const api = this._api;
        Object3D.DefaultUp.copy(Vector3Const.up);

        // @ts-ignore will be replaced with import.meta.url by rollup
        api.staticRootUrl = settingsProxy.settings.staticRootUrl || window.DEFAULT_STATIC_ROOT_URL || "/";

        api.container = document.createElement("main");
        api.container.setAttribute("id", "content");
        api.container.appendChild(document.createElement("canvas"));

        api.eventDispatcher = new EventDispatcher(this);
        api.settingsProxy = settingsProxy;
        api.scene = new Scene();

        api.selection = new Selection();
        api.selection.startSelectionEventEmitter(api.eventDispatcher);
        api.models = new Models(api.scene, api.selection);
        api.plugins = new ApiContainer<WebglViewerPlugin>();

        api.camera = new WVCamera(this._api);

        api.picker = new Picker(api.camera, api.models, api.container, api.settingsProxy);
        api.renderingManager = new RenderingManager(api);
        api.htmlElementPositioner = new HtmlElementPositioner(api);

        api.inputHandler = new InputHandler(
            api.picker,
            api.container
        );

        this._publicCamera = new PublicCamera(api.camera, api.models, api.settingsProxy);
        this._publicCamera.subscribeKeys(api.inputHandler, api.selection);
        api.cursor = new Cursor3D(api);

        api.toolManager = new ToolManager(api);

        api.models.worldBoundingBox.subscribe(() => api.renderingManager.redraw());
        api.models.worldBoundingBox.subscribe(aabb => api.picker.setWorldBoundingBox(aabb));
        api.analytics = new Analytics(api.settingsProxy);

        api.textureGenerator = new TextureGenerator();

        this.createShadowDom();
        this._api.renderingManager.start();
    }

    private createShadowDom(): void {
        this.attachShadow({mode: 'open'});
        // noinspection CssInvalidPropertyValue
        this.shadowRoot.innerHTML += `
            <style>
                :host {
                    display: block;
                    box-sizing: border-box;
                    height: 100%;
                    width: 100%;
                }

                main {
                    box-sizing: border-box;
                    height: 100%;
                    width: 100%;
                    cursor: default;
                    overflow: hidden;
                    position: relative;
                }

                canvas {
                    width: 100%;
                    height: 100%;
                    display: block;
                    position: relative;
                }

                .grab {
                    cursor: -webkit-grab !important;
                    cursor: -moz-grab !important;
                    cursor: grab !important;
                }

                .grabbing {
                    cursor: -webkit-grabbing !important;
                    cursor: -moz-grabbing !important;
                    cursor: grabbing !important;
                }

                .default {
                    cursor: default;
                }
            </style>`;
        this.shadowRoot.appendChild(this._api.container);
    }

    public initApiForJsdom(): void {
        const api: Api = {};
        this._api = api;

        api.registry = new Registry();
    }

    set activeTool(tool: string) {
        this._api.toolManager.activeTool = tool;
    }

    get activeTool(): string {
        return this._api.toolManager.activeTool;
    }

    get tools(): ApiContainer<Tool> {
        return this._api.toolManager.tools;
    }

    get plugins(): ApiContainer<WebglViewerPlugin> {
        return this._api.plugins;
    }

    async screenshot(): Promise<Blob> {
        return this._api.renderingManager.screenshot();
    }

    get selection(): Selection {
        return this._api.selection;
    }

    get camera(): PublicCamera {
        return this._publicCamera;
    }

    get vr(): VRManager {
        return this._api.renderingManager.vr;
    }
}

// Using eval to allow code to be removed by tree-shaking during build
// tslint:disable-next-line:no-eval
eval("if (window.customElements && typeof WebglViewer !== 'undefined') window.customElements.define('webgl-viewer', WebglViewer)");
